<template>
  <div class="orders-page">
    <h1>Meus Pedidos</h1>
    <div class="order" v-for="order in orders" :key="order.id">
      <h2>{{ order.name }}</h2>
      <p>Valor Total: R$ {{ order.total.toFixed(2) }}</p>
      <p>Data de Compra: {{ new Date(order.date).toLocaleDateString() }}</p>
      <p>Quantidade: {{ order.quantity }}</p>
      <p>Status do Pagamento: <span :class="{'status-label': true, [order.status.toLowerCase()]: true}">{{ order.status }}</span></p>
      <button v-if="order.status === 'Processando'" @click="cancelOrder(order.id)">Cancelar Produto</button>
    </div>
    <button class="exit-button" @click="ToProducts">Voltar</button>
  </div>
</template>

<script>
export default {
  name: 'PageMyOrders',
  data() {
    return {
      orders: [
        { id: 1, name: 'Pedido 1', total: 120.99, date: '2023-12-01', quantity: 2, status: 'Processando' },
        { id: 2, name: 'Pedido 2', total: 89.99, date: '2023-11-21', quantity: 1, status: 'Concluído' },
        { id: 3, name: 'Pedido 3', total: 49.99, date: '2023-11-15', quantity: 3, status: 'Cancelado' },
        { id: 4, name: 'Pedido 4', total: 120.99, date: '2023-12-01', quantity: 2, status: 'Processando' },
        { id: 5, name: 'Pedido 5', total: 89.99, date: '2023-11-21', quantity: 1, status: 'Concluído' },
        { id: 6, name: 'Pedido 6', total: 49.99, date: '2023-11-15', quantity: 3, status: 'Cancelado' },
      ]
    };
  },
  methods: {
    cancelOrder(id) {
      const order = this.orders.find(o => o.id === id);
      if (order && order.status === 'Processando') {
        order.status = 'Cancelado'; // Simula o cancelamento do pedido
        alert('Pedido cancelado com sucesso!');
      }
    },
    ToProducts() {
      this.$router.push({ name: 'PageProducts' });
    }
  }
}
</script>


<style scoped>
.orders-page {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  font-family: 'Arial', sans-serif;
  background-size: cover;
}

.order {
  background-color: rgba(255, 255, 255, 0.9);
  border: none;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 12px;
  transition: box-shadow 0.3s ease-in-out;
}

.order:hover {
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
}

.order h2 {
  color: #333;
  font-size: 24px;
  margin-bottom: 10px;
}

.order p {
  font-size: 16px;
  color: #666;
  margin: 5px 0;
}

.status-label {
  padding: 5px 15px;
  border-radius: 15px;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
}

.processando {
  background-color: #f0ad4e;
}

.cancelado {
  background-color: #d9534f;
}

.concluído {
  background-color: #5cb85c;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.2s;
  outline: none;
}

button:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
}

button:active {
  transform: translateY(1px);
}

.exit-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 10px 20px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  transition: background-color 0.3s;
}

.exit-button:hover {
  background-color: #c82333;
}
</style>